.buttonContainer {
  margin: 2rem;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  background: radial-gradient( #8F400D, #D4AB42);
  border: 1px solid #1f2027;
  border-radius: 10px;
  margin: auto;
  width: 15rem;
  height: 3rem;
  color: #070707;
  box-shadow: -5px -3px 10px 2px rgba(255, 255, 255, 0.08),
    5px 3px 8px 2px rgba(5, 4, 4, 0.466);
  font-size: 23px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
